import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TeacherPage } from './teacher.page';

const routes: Routes = [
  {
    path: 'teacher',
    component: TeacherPage,
    children:
    [
      {
        path: 'teacher-account',
        children: [
          {
          path: '',
          loadChildren: () => import('./Pages/teacher-account/teacher-account.module').then( m => m.TeacherAccountPageModule)
          },
          {
            path: ":student",
            loadChildren: () => import('src/app/members/teacher/Pages/teacher-account/reports/reports.module').then( m => m.ReportsPageModule)
          },
        ]
      },
      {
        path: 'class-room',
        loadChildren: () => import('./Pages/class-room/class-room.module').then( m => m.ClassRoomPageModule)
      },
      {
        path: 'calendar',
        loadChildren: () => import('./Pages/calendar/calendar.module').then( m => m.CalendarPageModule)
      },
      {
        path:'t-space',
        loadChildren:() => import('src/app/members/teacher/Pages/t-space/t-space.module').then(m=>m.TSpacePageModule)
      },
    ]
  },
  {
    path:'',
    redirectTo:'/teacher/teacher-account',
    pathMatch:'full'
  }
  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TeacherPageRoutingModule {}
