import { Component, OnInit } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { KillAllService } from 'src/app/services/killAll.service';
import { EjerciciosService } from '../student/Others/Services/ejercicios/ejercicios.service';


@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.page.html',
  styleUrls: ['./teacher.page.scss'],
})
export class TeacherPage implements OnInit {
  ejercicios = []
  numberHints: string;
  hintsClick: string;
  fails: string;
  constructor(private ejerciosService: EjerciciosService,private authService: AuthenticationService, private alertCtrl: AlertController, private killAll: KillAllService ) { }

  ngOnInit() {
    
    
    //Mock to show a first demo info
    this.numberHints=localStorage.getItem('numberHints');
    this.hintsClick=localStorage.getItem('hintsClick');
    this.fails=localStorage.getItem('fails');
  }
  logout(){
    this.authService.logout();
  }
  killEverything(){
    this.killAll.kill()
  }

  
  //Alerta para crear nuevos ejercicios dentro de la página web (En construcción)
  async openAlert() {
    const alert = await this.alertCtrl.create({
      header: '¡Nuevo ejercicio!',
      inputs: [
        {
          name: 'id',
          type: 'text',
          placeholder: 'Id'
        },
        {
          name: 'ob',
          type: 'text',
          placeholder: 'ob'
        },
        {
          name: 'title',
          type: 'text',
          placeholder: 'Título'
        },
        {
          name: 'imageURL',
          type: 'text',
          placeholder: 'Imagen URL'
        },
        {
          name: 'enunciado',
          type: 'text',
          placeholder: 'Enunciado'
        },
        {
          name: 'subtítulo',
          type: 'text',
          placeholder: 'Subtítulo'
        },
        {
          name: 'opciones',
          type: 'textarea',
          placeholder: 'Opciones de respuesta'
        },
        {
          name: 'pills',
          type: 'textarea',
          placeholder: 'Píldora informática'
        },
        {
          name: 'web',
          type: 'text',
          placeholder: 'Web de información'
        },
        {
          name: 'lat',
          type: 'text',
          placeholder: 'Web de información'
        },
        {
          name: 'long',
          type: 'text',
          placeholder: 'Web de información'
        },
        {
          name: 'popup',
          type: 'text',
          placeholder: 'Web de información'
        },
        {
          name: 'tooltip',
          type: 'text',
          placeholder: 'Web de información'
        }
      ],
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            console.log('Te has quedado sin nuevo ejercicio');
          }
        }, {
          text: 'Crear',
          handler: (data ) => {
           this.addEjercicioApi(data.id, data.ob, data.theme,data.title, data.imageURL, data.statement, data.subtitle, data.options,data.correcto, data.pills, data.web, data.lat ,data.long, data.popup, data.tooltip, data.difficulty,data.energy,data.hints,data.comp);
          }
        }
      ]
    });
    await alert.present();
  }
  //Método POST para crear nuevos ejercicios dentro de la página web (En construcción)
  addEjercicioApi(
      id: string,
      ob:string,
      theme:string,
      title: string,
      imageURL: string,
      statement: string,
      subtitle: string,
      options: string[],
      correcto:string,
      pills: string,
      web: string,
      lat: string[],
      long: string[],
      popup: string[],
      tooltip: string[],
      difficulty:string,
      energy:number,
      hints:string[],
      comp:boolean ){
    const nuevo = {
      id,
      ob,
      theme,
      title,
      imageURL,
      statement,
      subtitle,
      options,
      correcto,
      pills,
      web,
      lat,
      long,
      popup,
      tooltip,
      difficulty,
      energy,
      hints,
      comp,
    };
    this.ejerciosService.addEjerciciosAPI(nuevo)
    .subscribe((nuevoEjercicio) => {
      this.ejercicios.unshift(nuevoEjercicio);
    });
  }  
}
